import BigNumber from "bignumber.js";
import React from "react";
import { CardBody, Flex, Text, CardRibbon, HelpIcon, useTooltip } from "@plasmaswap/uikit";
import UnlockButton from "components/UnlockButton";
import { getAddress } from "utils/addressHelpers";
import { BIG_ZERO } from "utils/bigNumber";
import { useGetApiPrice } from "state/hooks";
import { Pool } from "state/types";
import styled from "styled-components";
import AprRow from "./AprRow";
import { StyledCard, StyledCardInner } from "./StyledCard";
import CardFooter from "./CardFooter";
import StyledCardHeader from "./StyledCardHeader";
import CardActions from "./CardActions";

const HarvestLockupRow = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Col1 = styled.div`
  display: flex;
`;

const Col2 = styled.div`
  display: flex;
`;

const ReferenceElement = styled.div`
  display: inline-block;
`;

const PoolCard: React.FC<{ pool: Pool; account: string }> = ({ pool, account }) => {
  const { allocPoint, sousId, stakingToken, earningToken, isFinished, userData } = pool;
  const stakedBalance = userData?.stakedBalance ? new BigNumber(userData.stakedBalance) : BIG_ZERO;
  const accountHasStakedBalance = stakedBalance.gt(0);
  const stakingTokenPrice = useGetApiPrice(stakingToken.address ? getAddress(stakingToken.address) : "");

  const { targetRef, tooltip, tooltipVisible } = useTooltip(
    "How long you must wait until you are able to harvest again",
    { placement: "top", tooltipOffset: [0, 10] },
  );

  return (
    <StyledCard
      isFinished={isFinished && sousId !== 0}
      ribbon={isFinished && <CardRibbon variantColor="textDisabled" text="Finished" />}
    >
      <StyledCardInner>
        <StyledCardHeader
          isStaking={accountHasStakedBalance}
          earningTokenSymbol={earningToken.symbol}
          stakingTokenSymbol={stakingToken.symbol}
          isFinished={isFinished && sousId !== 0}
          allocPoint={allocPoint}
        />

        <CardBody>
          <AprRow pool={pool} stakingTokenPrice={stakingTokenPrice} />

          <HarvestLockupRow>
            <Col1>
              Harvest Lockup:
            </Col1>
            <Col2>
              <ReferenceElement ref={targetRef}>
                <HelpIcon color="textSubtle" />
              </ReferenceElement>
              {tooltipVisible && tooltip}
              {pool.harvestLockup}
            </Col2>
          </HarvestLockupRow>

          <Flex mt="24px" flexDirection="column">
            {account ? (
              <CardActions pool={pool} stakedBalance={stakedBalance} stakingTokenPrice={stakingTokenPrice} />
            ) : (
              <>
                <Text mb="10px" textTransform="uppercase" fontSize="12px" color="textSubtle" bold>
                  Start earning
                </Text>
                <UnlockButton />
              </>
            )}
          </Flex>
        </CardBody>
        <CardFooter pool={pool} account={account} />
      </StyledCardInner>
    </StyledCard>
  );
};

export default PoolCard;
