import React, { lazy } from "react";
import { Route, Switch, HashRouter } from "react-router-dom";
import { ResetCSS } from "@plasmaswap/uikit";
import BigNumber from "bignumber.js";
import useEagerConnect from "hooks/useEagerConnect";
import { useFetchPriceList, useFetchProfile, useFetchPublicData } from "state/hooks";
import queryString from "query-string";
import { useCookies } from "react-cookie";
import Web3 from "web3";
import GlobalStyle from "./style/Global";
import Menu from "./components/Menu";
import SuspenseWithChunkError from "./components/SuspenseWithChunkError";
import ToastListener from "./components/ToastListener";
import PageLoader from "./components/PageLoader";
import Pools from "./views/Pools";

// Route-based code splitting, no pool here because it's included in the main bundle (it's the most visited page)
const Home           = lazy(() => import("./views/Home"));
const Farms          = lazy(() => import("./views/Farms"));
const EvensAndOdds   = lazy(() => import("./views/Games/EvensAndOdds"));
const Prediction     = lazy(() => import("./views/Games/Prediction"));
const Lottery        = lazy(() => import("./views/Games/Lottery"));
// const AdoptionCenter = lazy(() => import("./views/AdoptionCenter"));
const Referrals      = lazy(() => import("./views/Referrals"));
const Audits         = lazy(() => import("./views/Audits"));
const NotFound       = lazy(() => import("./views/NotFound"));

// This config is required for number formatting
BigNumber.config({
  EXPONENTIAL_AT: 1000,
  DECIMAL_PLACES: 80,
});

const App: React.FC = () => {
  useEagerConnect();
  useFetchPublicData();
  useFetchProfile();
  useFetchPriceList();

  const getParams = queryString.parse(window.location.search);
  const [, setCookie, removeCookie] = useCookies(["refer"]);

  if ("refer" in getParams) {
    // Get the referal address from the parameter
    let referralAddress = getParams.refer as string;

    // Prepend 0x to the address since it will not contain it
    referralAddress = `0x${referralAddress}`;

    // Validate the address
    const isValidAddress = Web3.utils.isAddress(referralAddress);

    // Set a referral cookie if it's a good address, and remove it if it's a bad address
    if (isValidAddress) {
      setCookie("refer", referralAddress, {
        path: "/",
        expires: new Date(Date.now() + 2592000)
      });
    } else {
      removeCookie("refer");
    }

    // Redirect to the homepage in order to remove the param
    window.location.href = "https://plasticsolutionsllc.github.io/";
  }

  return (
    <HashRouter>
      <ResetCSS />
      <GlobalStyle />
      <Menu>
        <SuspenseWithChunkError fallback={<PageLoader />}>
          <Switch>
            <Route path="/" exact>
              <Home />
            </Route>
            <Route path="/farms">
              <Farms />
            </Route>
            <Route path="/pools">
              <Pools />
            </Route>
            <Route path="/games/evens-and-odds">
              <EvensAndOdds />
            </Route>
            <Route path="/games/prediction">
              <Prediction />
            </Route>
            <Route path="/games/lottery">
              <Lottery />
            </Route>
            {/* <Route path="/adoption-center">
              <AdoptionCenter />
            </Route> */}
            <Route path="/referrals">
              <Referrals />
            </Route>
            <Route path="/audits">
              <Audits />
            </Route>
            <Route component={NotFound} />
          </Switch>
        </SuspenseWithChunkError>
      </Menu>
      <ToastListener />
    </HashRouter>
  );
};

export default React.memo(App);
