import BigNumber from "bignumber.js";
import erc20 from "config/abi/erc20.json";
import masterchefABI from "config/abi/masterchef.json";
import multicall from "utils/multicall";
import { BIG_TEN } from "utils/bigNumber";
import { getAddress, getMasterChefAddress } from "utils/addressHelpers";
import { FarmConfig } from "config/constants/types";
import { DEFAULT_TOKEN_DECIMAL } from "config";

const fetchFarms = async (farmsToFetch: FarmConfig[], address: string) => {
  const data = await Promise.all(
    farmsToFetch.map(async (farmConfig) => {
      const lpAddress = getAddress(farmConfig.lpAddresses);
      const masterChefAddress = getMasterChefAddress();
      const calls = [
        // Balance of token in the LP contract
        {
          name: "balanceOf",
          address: getAddress(farmConfig.token.address),
          params: [lpAddress],
        },
        // Balance of quote token on LP contract
        {
          name: "balanceOf",
          address: getAddress(farmConfig.quoteToken.address),
          params: [lpAddress],
        },
        // Balance of LP tokens in the master chef contract
        {
          name: "balanceOf",
          address: lpAddress,
          params: [masterChefAddress],
        },
        // Total supply of LP tokens
        {
          name: "totalSupply",
          address: lpAddress,
        },
        // Token decimals
        {
          name: "decimals",
          address: getAddress(farmConfig.token.address),
        },
        // Quote token decimals
        {
          name: "decimals",
          address: getAddress(farmConfig.quoteToken.address),
        },
      ]

      const [
        tokenBalanceLP,
        quoteTokenBalanceLP,
        lpTokenBalanceMC,
        lpTotalSupply,
        tokenDecimals,
        quoteTokenDecimals
      ] = await multicall(erc20, calls);

      // Ratio in % a LP tokens that are in staking, vs the total number in circulation
      const lpTokenRatio = new BigNumber(lpTokenBalanceMC).div(new BigNumber(lpTotalSupply))

      // Total value in staking in quote token value
      const lpTotalInQuoteToken = new BigNumber(quoteTokenBalanceLP)
        .div(DEFAULT_TOKEN_DECIMAL)
        .times(new BigNumber(2))
        .times(lpTokenRatio)

      // Amount of token in the LP that are considered staking (i.e amount of token * lp ratio)
      const tokenAmount = new BigNumber(tokenBalanceLP).div(BIG_TEN.pow(tokenDecimals)).times(lpTokenRatio)

      const quoteTokenAmount = new BigNumber(quoteTokenBalanceLP)
        .div(BIG_TEN.pow(quoteTokenDecimals))
        .times(lpTokenRatio)

      const [info, totalAllocPointsFarms, canHarvest] = await multicall(masterchefABI, [
        {
          name: "pools",
          address: masterChefAddress,
          params: [farmConfig.pid],
        },
        {
          name: "totalAllocPointsFarms",
          address: masterChefAddress,
        },
        {
          name: "canHarvest",
          address: masterChefAddress,
          params: [farmConfig.pid, address],
        }
      ]);

      const allocPoint = new BigNumber(info.allocPoint._hex);
      const poolWeight = allocPoint.div(new BigNumber(totalAllocPointsFarms));
      const harvest = canHarvest[0];

      return {
        ...farmConfig,
        tokenAmount: tokenAmount.toJSON(),
        quoteTokenAmount: quoteTokenAmount.toJSON(),
        lpTotalSupply: new BigNumber(lpTotalSupply).toJSON(),
        lpTotalInQuoteToken: lpTotalInQuoteToken.toJSON(),
        tokenPriceVsQuote: quoteTokenAmount.div(tokenAmount).toJSON(),
        poolWeight: poolWeight.toJSON(),
        multiplier: `${allocPoint.div(100).toString()}x`,
        canHarvest: harvest
      }
    })
  );

  return data;
};

export default fetchFarms;
