import BigNumber from "bignumber.js";
import poolsConfig from "config/constants/pools";
import sousChefABI from "config/abi/sousChef.json";
import cakeABI from "config/abi/cake.json";
import multicall from "utils/multicall";
import masterchefABI from "config/abi/masterchef.json";
import { getAddress, getMasterChefAddress } from "utils/addressHelpers";
import { BIG_ZERO } from "utils/bigNumber";
import { getSouschefV2Contract } from "utils/contractHelpers";

export const fetchPoolsBlockLimits = async () => {
  const poolsWithEnd = poolsConfig.filter((p) => p.sousId !== 0)
  const callsStartBlock = poolsWithEnd.map((poolConfig) => {
    return {
      address: getAddress(poolConfig.contractAddress),
      name: "startBlock",
    }
  })
  const callsEndBlock = poolsWithEnd.map((poolConfig) => {
    return {
      address: getAddress(poolConfig.contractAddress),
      name: "bonusEndBlock",
    }
  })

  const starts = await multicall(sousChefABI, callsStartBlock)
  const ends = await multicall(sousChefABI, callsEndBlock)

  return poolsWithEnd.map((cakePoolConfig, index) => {
    const startBlock = starts[index]
    const endBlock = ends[index]
    return {
      sousId: cakePoolConfig.sousId,
      startBlock: new BigNumber(startBlock).toJSON(),
      endBlock: new BigNumber(endBlock).toJSON(),
    }
  })
}

export const fetchPoolsTotalStaking = async (address: string) => {
  const nonBnbPools = poolsConfig.filter((p) => p.stakingToken.symbol !== "BNB");
  const callCakeABI = nonBnbPools.map((pool) => {
    return {
      name: "balanceOf",
      address: getAddress(pool.stakingToken.address),
      params: [getAddress(pool.contractAddress)]
    };
  });
  const a = await multicall(cakeABI, callCakeABI);
  const b = a.map((input) => {
    return {
      totalStaked: new BigNumber(input).toJSON()
    };
  });

  const callMasterChefABI1 = nonBnbPools.map((pool) => {
    return {
      address: getMasterChefAddress(),
      name: "pools",
      params: [pool.sousId]
    };
  });

  const callMasterChefABI2 = nonBnbPools.map((pool) => {
    return {
      name: "canHarvest",
      address: getMasterChefAddress(),
      params: [pool.sousId, address]
    };
  });

  const c = await multicall(masterchefABI, callMasterChefABI1);
  const d = await multicall(masterchefABI, callMasterChefABI2);
  const e = c.map((input) => {return {allocPoint: new BigNumber(input.allocPoint._hex).toJSON()};});
  const f = d.map((input) => {return {canHarvest: input.canHarvest};});
  const data = [];

  for (let i = 0; i < nonBnbPools.length; i++) {
    const { sousId } = nonBnbPools[i];

    data.push({
      sousId,
      totalStaked: b[i].totalStaked,
      allocPoint: e[i].allocPoint,
      canHarvest: f[i].canHarvest
    });
  }

  return data;
}

export const fetchPoolsTotalStakingPrev = async () => {
  const nonBnbPools = poolsConfig.filter((p) => p.stakingToken.symbol !== "BNB")
  const callsNonBnbPools = nonBnbPools.map((poolConfig) => {
    return {
      address: getAddress(poolConfig.stakingToken.address),
      name: "balanceOf",
      params: [getAddress(poolConfig.contractAddress)],
    }
  })

  const nonBnbPoolsTotalStaked = await multicall(cakeABI, callsNonBnbPools)

  return [
    ...nonBnbPools.map((p, index) => ({
      sousId: p.sousId,
      totalStaked: new BigNumber(nonBnbPoolsTotalStaked[index]).toJSON(),
    }))
  ]
}

export const fetchPoolStakingLimit = async (sousId: number): Promise<BigNumber> => {
  try {
    const sousContract = getSouschefV2Contract(sousId)
    const stakingLimit = await sousContract.methods.poolLimitPerUser().call()
    return new BigNumber(stakingLimit)
  } catch (error) {
    return BIG_ZERO
  }
}

export const fetchPoolsStakingLimits = async (poolsWithStakingLimit: number[]): Promise<{ [key: string]: BigNumber }> => {
  const validPools = poolsConfig
    .filter((p) => p.stakingToken.symbol !== "BNB" && !p.isFinished)
    .filter((p) => !poolsWithStakingLimit.includes(p.sousId))

  const stakingLimitPromises = validPools.map((validPool) => fetchPoolStakingLimit(validPool.sousId))
  const stakingLimits = await Promise.all(stakingLimitPromises)

  return stakingLimits.reduce((accum, stakingLimit, index) => {
    return {
      ...accum,
      [validPools[index].sousId]: stakingLimit,
    }
  }, {})
}
