/* eslint-disable no-nested-ternary */
import React from "react";
import { Flex, Text, Button, Heading, useModal, Skeleton } from "@plasmaswap/uikit";
import BigNumber from "bignumber.js";
import { Token } from "config/constants/types";
import { getAddress } from "utils/addressHelpers";
import { getFullDisplayBalance, getBalanceNumber, formatNumber } from "utils/formatBalance";
import { useGetApiPrice } from "state/hooks";
import Balance from "components/Balance";
import CollectModal from "../Modals/CollectModal";

interface HarvestActionsProps {
  nextHarvest: number
  earnings: BigNumber
  earningToken: Token
  sousId: number
  isBnbPool: boolean
  isLoading?: boolean
}

const HarvestActions: React.FC<HarvestActionsProps> = ({ nextHarvest, earnings, earningToken, sousId, isBnbPool, isLoading = false }) => {
  const earningTokenPrice = useGetApiPrice(earningToken.address ? getAddress(earningToken.address) : "");
  const earningTokenBalance = getBalanceNumber(earnings, earningToken.decimals);
  const earningTokenDollarBalance = getBalanceNumber(earnings.multipliedBy(earningTokenPrice), earningToken.decimals);
  const fullBalance = getFullDisplayBalance(earnings, earningToken.decimals);
  const formattedBalance = formatNumber(earningTokenBalance, 3, 3);
  const earningsDollarValue = formatNumber(earningTokenDollarBalance);
  const hasEarnings = earnings.toNumber() > 0;
  const isCompoundPool = sousId === 0;
  const [onPresentCollect] = useModal(
    <CollectModal
      formattedBalance={formattedBalance}
      fullBalance={fullBalance}
      earningToken={earningToken}
      earningsDollarValue={earningsDollarValue}
      sousId={sousId}
      isBnbPool={isBnbPool}
      isCompoundPool={isCompoundPool}
    />,
  );

  const now = Math.round(Date.now() / 1000) - 30;
  const secondsRemaining = nextHarvest - now;
  const canHarvest = (secondsRemaining <= 0);
  const minutesRemaining = ((Math.ceil(secondsRemaining / 60) > 119) ? 119 : Math.ceil(secondsRemaining / 60));
  const lockupString = `Lockup: ${minutesRemaining} min${(minutesRemaining > 1) ? "s" : ""}`;

  return (
    <Flex flexDirection="column" mb="16px">
      <Flex justifyContent="space-between" alignItems="center">
        <Flex flexDirection="column">
          {isLoading ? (
            <Skeleton width="80px" height="48px" />
          ) : (
            <>
              {hasEarnings ? (
                <Balance bold fontSize="20px" decimals={5} value={earningTokenBalance} />
              ) : (
                <Heading color="white">0</Heading>
              )}
              <Text fontSize="12px" color="#51a997">
                ~
                {hasEarnings ? (
                  <Balance
                    display="inline"
                    fontSize="12px"
                    color="textSubtle"
                    decimals={2}
                    value={earningTokenDollarBalance}
                    unit=" USD"
                  />
                ) : (
                  "0 USD"
                )}
              </Text>
            </>
          )}
        </Flex>
        <Flex>
          <Button disabled={!hasEarnings || !canHarvest || nextHarvest === 0} onClick={onPresentCollect}>
            {canHarvest ? (isCompoundPool ? "Collect" : "Harvest") : lockupString}
          </Button>
        </Flex>
      </Flex>
    </Flex>
  );
};

export default HarvestActions;
