import React from 'react';
import { Flex, IconButton, useModal, CalculateIcon, Skeleton } from '@plasmaswap/uikit';
import { getBalanceNumber } from 'utils/formatBalance';
import { getPoolApr } from 'utils/apr';
// import { getAddress } from 'utils/addressHelpers';
// import { useGetApiPrice } from 'state/hooks';
import Balance from 'components/Balance';
import ApyCalculatorModal from 'components/ApyCalculatorModal';
import { Pool } from 'state/types';
import { BASE_EXCHANGE_URL } from 'config';

interface AprRowProps {
  pool: Pool
  stakingTokenPrice: number
  isAutoVault?: boolean
  compoundFrequency?: number
  performanceFee?: number
}

const AprRow: React.FC<AprRowProps> = ({
  pool,
  stakingTokenPrice,
  compoundFrequency = 1,
  performanceFee = 0
}) => {
  const { stakingToken, earningToken, totalStaked, isFinished, tokenPerBlock } = pool;

  // TODO: For testnets, set the price of all earning tokens to $0.01
  // const earningTokenPrice = useGetApiPrice(earningToken.address ? getAddress(earningToken.address) : '');
  const earningTokenPrice = 0.00000001;
  // eslint-disable-next-line no-param-reassign
  stakingTokenPrice = 0.01;

  const isHighValueToken = Math.round(earningTokenPrice / 1000) > 0; // Special handling for high-value tokens where the daily rewards for $1000 will be less than 0.001 of that token

  const apr = getPoolApr(
    stakingTokenPrice,
    earningTokenPrice,
    getBalanceNumber(totalStaked, stakingToken.decimals),
    parseFloat(tokenPerBlock),
  );

  const apyModalLink = stakingToken.address &&
    `${BASE_EXCHANGE_URL}/#/swap?outputCurrency=${stakingToken.address[process.env.REACT_APP_CHAIN_ID]}`;

  const [onPresentApyModal] = useModal(
    <ApyCalculatorModal
      tokenPrice={earningTokenPrice}
      apr={apr}
      linkLabel={`Get ${stakingToken.symbol}`}
      linkHref={apyModalLink || BASE_EXCHANGE_URL}
      earningTokenSymbol={earningToken.symbol}
      roundingDecimals={isHighValueToken ? 4 : 2}
      compoundFrequency={compoundFrequency}
      performanceFee={performanceFee}
    />,
  );

  return (
    <Flex alignItems="center" justifyContent="space-between">
      APR:
      {isFinished || !apr ? (
        <Skeleton width="82px" height="32px" />
      ) : (
        <Flex alignItems="center">
          <IconButton onClick={onPresentApyModal} variant="text" scale="sm">
            <CalculateIcon color="textSubtle" width="18px" />
          </IconButton>
          <Balance
            isDisabled={isFinished}
            value={apr}
            decimals={2}
            unit="%"
          />
        </Flex>
      )}
    </Flex>
  );
}

export default AprRow;
