import React from "react";
import { Modal, Text, Button } from "@plasmaswap/uikit";
import { BASE_EXCHANGE_URL } from "config";
import useTheme from "hooks/useTheme";

interface NotEnoughTokensModalProps {
  tokenSymbol: string
  onDismiss?: () => void
}

const NotEnoughTokensModal: React.FC<NotEnoughTokensModalProps> = ({ tokenSymbol, onDismiss }) => {
  const { theme } = useTheme();

  return (
    <Modal
      title={`${tokenSymbol} required`}
      onDismiss={onDismiss}
      headerBackground={theme.colors.gradients.cardHeader}
    >
      <Text color="failure" bold>
        Insufficient { tokenSymbol } balance
      </Text>
      <Text mt="24px">You&lsquo;ll need { tokenSymbol } to stake in this pool</Text>
      <Text>
        Buy some { tokenSymbol }, or make sure your { tokenSymbol } isnt in another pool or LP
      </Text>
      <Button mt="24px" as="a" external href={BASE_EXCHANGE_URL}>
        Buy {tokenSymbol}
      </Button>
      <Button variant="text" onClick={onDismiss}>
        Close window
      </Button>
    </Modal>
  );
};

export default NotEnoughTokensModal;
